import React, {useEffect, useState} from 'react'
import s2 from '../../s1-main/App.module.css'
import s from './HW15.module.css'
import axios from 'axios'
import SuperPagination from './common/c9-SuperPagination/SuperPagination'
import {useSearchParams} from 'react-router-dom'
import SuperSort from './common/c10-SuperSort/SuperSort'
import { Loader } from '../hw10/Loader'

/*
* 1 -  SuperPagination//
* 2 -  SuperSort
* 3 -  pureChange 
* 3 -  sendQuery, onChangePagination, onChangeSort  HW15
* 4 -      
* 5 -  HW15  HW5/pages/JuniorPlus
* */

type TechType = {
    id: number
    tech: string
    developer: string
}

type ParamsType = {
    sort: string
    page: number
    count: number
}

const getTechs = async(params: ParamsType) => {
    return await axios
        .get<{ techs: TechType[], totalCount: number }>(
            'https://incubator-personal-page-back.herokuapp.com/api/3.0/homework/test3',
            {params}
        )
        .catch((e) => {
            alert(e.response?.data?.errorText || e.message)
        })
}

const HW15 = () => {
    const [sort, setSort] = useState('')
    const [page, setPage] = useState(1)
    const [count, setCount] = useState(4)
    const [idLoading, setLoading] = useState(false)
    const [totalCount, setTotalCount] = useState(100)
    const [searchParams, setSearchParams] = useSearchParams()
    const [techs, setTechs] = useState<TechType[]>([])

    const sendQuery = (params: ParamsType) => {
        setLoading(true)
        getTechs(params)
            .then((res) => {
                //  

                //   
                res && setTechs(res.data.techs);
                res && setTotalCount(res.data.totalCount)
                //
                setLoading(false)
            })
    }

    const onChangePagination = (newPage: number, newCount: number) => {
        //  

        setPage(newPage)
        setCount(newCount)

        sendQuery({sort: sort, page: newPage, count: newCount})
        const params = {page: JSON.stringify(newPage), count: JSON.stringify(newCount)}
        setSearchParams(params)

        //
    }

    const onChangeSort = (newSort: string) => {
        //  

        setSort(newSort)
        setPage(1) //     1 

        sendQuery({sort: sort, page: page, count: count})
        setSearchParams({sort: sort, page: JSON.stringify(page), count: JSON.stringify(count)})
    }

    const changeCount = (number: number) => {
        setCount(number)
        const params = {sort: sort, page: page, count: number}
        setSearchParams({sort: sort, page: JSON.stringify(page), count: JSON.stringify(count)})
        sendQuery(params)
    }

    useEffect(() => {
        const params = Object.fromEntries(searchParams)
        sendQuery({sort: params.sort, page: +params.page, count: +params.count})
        setPage(+params.page || 1)
        setCount(+params.count || 4)
    }, [])

    /* const filteredTechs = (): TechType[] => {
        if(sort == '') {
            return techs
        } else if (sort === '0tech' || sort === '0developer') { 
            return [...techs].sort((a, b) => a.id - b.id)
         } else if  (sort === '1tech' || sort === '1developer') {
            return [...techs].sort((a, b) => b.id - a.id)
        } else {
            return techs
        }
    }

    console.log(filteredTechs()); */
    

    const mappedTechs = techs.map(t => (
        <div key={t.id} className={s.row}>
            <div id={'hw15-tech-' + t.id} className={s.tech}>
                {t.tech}
            </div>

            <div id={'hw15-developer-' + t.id} className={s.developer}>
                {t.developer}
            </div>
        </div>
    ))

    return (
        <div id={'hw15'}>
            <div className={s2.hwTitle}>Homework #15</div>

            <div className={s2.hw}>
                <div className={s.paginationBlock}>

                {idLoading && <div id={'hw15-loading'} className={s.loading}><div style={{display: 'flex', justifyContent:'center', alignItems: 'center', marginTop: '10%'}}><Loader/></div></div>}

                <SuperPagination
                    page={page}
                    itemsCountForPage={count}
                    totalCount={totalCount}
                    onChange={onChangePagination}
                    changeCount={changeCount}//
                    count={count}//
                />

                <div className={s.rowHeader}>
                    <div className={s.techHeader}>
                        tech
                        <SuperSort sort={sort} value={'tech'} onChange={onChangeSort}/>
                    </div>

                    <div className={s.developerHeader}>
                        developer
                        <SuperSort sort={sort} value={'developer'} onChange={onChangeSort}/>
                    </div>
                </div>
                <div className={s.techItems}> 
                    { mappedTechs }
                </div>
            </div>
            </div>
        </div>
    )
}

export default HW15
